  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(16, 1fr); }
  .examples__gallery a {
    display: block;
    margin: 0; }
    .examples__gallery a img {
      display: block;
      width: 100%;
      height: 100%;
      margin: 0; }
    .examples__gallery a:nth-child(1) {
      grid-column: 1/5;
      grid-row: 1/12; }
    .examples__gallery a:nth-child(2) {
      grid-column: 5/10;
      grid-row: 1/7; }
    .examples__gallery a:nth-child(3) {
      grid-column: 10/13;
      grid-row: 1/7; }
    .examples__gallery a:nth-child(4) {
      grid-column: 1/5;
      grid-row: 12/17; }
    .examples__gallery a:nth-child(5) {
